.Contact{
    margin-top:8%;
    width:100%;
    height:100%;
    border: 5px solid black;
    background:#121212;
    color:white;
    text-align: center;

    display: grid;
    grid-template-columns: repeat(3, 4fr);
    grid-template-rows: minmax(300px, auto);
    grid-template-areas:
    "contact-title contact-title contact-title"
    "card1 card2 card3"
}
.Contact-Title{
    grid-area:contact-title; 
}
.Contact-Title h1{
    margin-top:5%;
    border: 5px solid white;
    margin-left:25%;
    margin-right:25%;
}
.Contact-Card{
    grid-area:card1;
}
.Contact-Card2{
    grid-area:card2;
    padding-top:20px;
}
.Contact-Card2 img{
    border-radius: 50%;
}
.Contact-Card3{
    grid-area:card3;
}

