
.Footer{
  border:5px solid black;
  color:white;
  background: url('../img/footer-background.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin:0%;
  width:auto;
  padding-bottom:50px;
  padding-left:25%;
  padding-right:25%;
  display: grid;
  grid-template-columns: repeat(6, 2fr);
  grid-template-rows: minmax(200px, auto);
  grid-template-areas:
  "title title title title title title"
  "col1 col1 col2 col2 col3 col3"
  
}
.footer-title{
    grid-area:title;
    text-align:center;
}
.footer-title h1{
    border: 5px solid white;
    padding:10px;
}
.col-1{
    grid-area:col1;
    min-width:max-content;
    margin:0px;
    padding:10px;
    /* border: 1px solid white;*/
    text-align:center;
}
.col-2{
    grid-area:col2;
    min-width:max-content;
    margin:0px;
    padding:10px;
    border: 3px double white;
    text-align:center;
}
.col-3{
    grid-area:col3;
    min-width: max-content;
    margin:0px;
    padding:10px;
    /*border: 1px solid white;*/
    text-align:center;
}
.Footer h1{
    color: white;
   
}
.Footer span{
    font-size: 15px;
}