.Login-Container{
    margin-top:8%;
    padding-top:10%;
    padding-bottom:10%;
    border: 5px solid black;
    background: #121212;
    
    display:grid;
    grid-template-columns: repeat(3, 4fr);
    grid-template-rows: minmax(500px, auto);
    grid-template-areas:
    ". form ."
}

.Login-Form{
    grid-area:form;
    border: 5px solid white;
    background: #121212;
    color: white
}
.Login-Form h1{
    text-align:center;
    border: 5px solid white;
    margin-left:25%;
    margin-right:25%;
    padding:10px;
}
.Login-Form form{
    margin-left: 25%;
    margin-right:25%;
}

.Login-Form input[type=submit]{
    background-color: white;
    color: black;
    border: none;
    padding: 15px 30px;
    text-decoration: none;
    font-weight: bold;
    margin: 4px 2px;
    cursor: pointer;
}
.Login-Form input[type=submit]:hover{
    background-color: black;
    color: white;
    border: none;
    padding: 15px 30px;
    text-decoration: none;
    font-weight: bold;
    margin: 4px 2px;
    cursor: pointer;
}